				className="halfBg"
				src="https://images.unsplash.com/photo-1544245607-99ac92ec1da5?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80"
